.maker {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: makerWhite;
  }
  
  .container {
    display: flex;
    flex: 1;
    min-height: 0;
  }
  
  @media screen and (max-width: 62rem) {
    .container {
      flex-direction: column;
    }
  }
  